    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.app .btn {
    width: 18%;
    height: 3rem;
    margin: 8px;
    padding-bottom: 15px;
    padding-top: 10px; 
    border-radius: 8px;
    font-size: 1.2rem;
    background: transparent;
    color: white;
    
}

.app .btn:hover {
    cursor: pointer;
    background: beige;
    color: #4f4f4f;
}


